// Cardboard Variables
$fixed-navbar-spacing: 3.375rem;

// font variable
$fontstack: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;

// Color Variables
$primary-color: #303F9F;
$dark-primary-color: #303F9F;
$light-primary-color: #C5CAE9;
$secondary-color: #6D6358;
$text-icon-color: #FFF;
$secondary-accent-color: #9E9E9E;
$primary-text: #212121;
$secondary-text: #757575;
$divider-color: #BDBDBD;

$off-white: #EDEFF0;
$off-white-alt: #F5F5F5;
$off-black: #374046;

// Bootstrap alert colors
$success-color: #3C763D;
$info-color: #31708F;
$warning-color: #8A6D3B;
$danger-color: #A94442;


// Bootstrap Breakpoints
// xs devices are less than 34rem
$media-sm: 576px;
$media-md: 768px;
$media-lg: 992px;
$media-xl: 1200px;

$media-max: 'max-width';
$media-min: 'min-width';

// Cardboard Mixins
@mixin boxshadow($offset-x, $offset-y, $blur-radius, $color: rgba(0, 0, 0, 0.3), $spread: 0px) {
	-moz-box-shadow: $offset-x $offset-y $blur-radius $spread $color;
	-webkit-box-shadow: $offset-x $offset-y $blur-radius $spread $color;
	box-shadow: $offset-x $offset-y $blur-radius $spread $color; }

@mixin center-image {
	display: block;
	margin: 0 auto 1rem; }

// Import Library
@import "cardboard_general";
@import "cardboard_bootstrap_reset";
@import "cardboard_markdown";
