// Nav Resets
body.frontend-body {
	nav.navbar {
		background-color: $primary-color;
		button.btn {
			background-color: $secondary-accent-color;
			color: $text-icon-color; } }

	nav.navbar-dark {
		.navbar-nav {
			.nav-link.active {
				color: $text-icon-color; } } } }

// For darker backend navbars, an off-white for body
body.backend-body {
	background-color: $off-white-alt; }

// Missing Utilities
.cardboard-img-circle {
	width: 100%;
	border-radius: 100%; }

// Without the important symbol so that jquery can animate its hidden property
.cardboard-hidden {
	display: none; }

// Color Resets
h1, h2, h3, h4, h5, h6 {
	color: $primary-text; }

p, pre, li {
	color: $secondary-text; }

.alert-success {
	p {
		color: $success-color; } }

.alert-info {
	p {
		color: $info-color; } }

.alert-warning {
	p {
		color: $warning-color; } }

.alert-danger {
	p {
		color: $danger-color; } }

.btn-oval {
	border-radius: 5rem; }

hr {
	color: $divider-color; }

a {
	color: $primary-color;
	&:hover {
		color: darken($primary-color, 15%);
		text-decoration: none; } }

.btn.btn-primary {
	background-color: $primary-color;
	border-color: darken($primary-color, 5%);
	&:hover {
		background-color: darken($primary-color, 15%);
		border-color: darken($primary-color, 20%); } }
