body {
	padding-top: 3.375rem;
	font-family: "Lucida Grande", Helvetica, Arial, sans-serif;

	.card-header.bg-primary {
		h3.text-white {
			color: $text-icon-color; } }

	table {
		tr.table-less-padding {
			th, td {
				padding: 0.5rem 0.75rem; }
			td input {
				width: 100%; } } }

	nav.footer {
		@include boxshadow(0, -1px, 2px); } }
