.cardboard-markdown {
	h1, h2, h3, h4, h5, h6 {
		margin-top: 1.5rem;
		margin-bottom: 1rem; }
	blockquote {
		padding: 0.5rem 1rem;
		margin-bottom: 1rem;
		padding-left: 0;
		text-align: right;
		border-right: 0.25rem solid $off-black; }
	pre {
		border: 1px solid $off-black;
		border-left: 3px solid $off-black;
		border-radius: 0.25rem;
		margin: 2rem 1rem;
		span.the-header-comment {
			display: block;
			background-color: $off-black;
			color: $text-icon-color;
			margin-bottom: -1rem;
			padding: 0.5rem 2rem 0.25rem; }
		code {
			padding: 0 2rem 2rem; } } }
