// body
// 	margin-top: $fixed-navbar-spacing
// 	.text-center
// 		text-align: center
body.no-scroll {
	overflow: hidden; }

// Centered Images
.carousel-centered-imgs {
	.carousel-item {
		img {
			@include center-image; } } }

// Bootstrap card styling
.card-normalized {
	border: none;
	-webkit-border-radius: 0px;
	-moz-border-radius: 0px;
	border-radius: 0px;
	.card-img {
		@extend .card-normalized;
		width: 100%; } }

.card-transparent {
	background-color: none;
	background-color: rgba(0, 0, 0, 0); }

.flash-messages {
	position: fixed;
	z-index: 4000;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100vw;
	height: 1px;
	.alert {
		margin: 3rem auto auto;
		width: 80vw; } }

// Form styling
label.cardboard-fa {
	width: 100%;
	cursor: pointer;
	input[type="checkbox"] {
		display: none; }
	input[type="checkbox"] + span:before {
		font-family: FontAwesome;
		content: "\f096";
		display: inline-block;
		letter-spacing: 0.5rem;
		color: $danger-color;
		margin-right: 2.25px; }
	input[type="checkbox"]:checked + span:before {
		content: "\f046";
		letter-spacing: 0.5rem;
		color: $success-color;
		margin-right: 0px; } }
.color-success {
	color: $success-color; }

// Angular and modals
[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
	display: none !important; }

.cardboard-modal {
	position: fixed;
	z-index: 3000;
	top: 0;
	right: 0;
	width: 100%;
	height: 100vh;
	overflow: auto;
	background-color: rgb(0,0,0);
	background-color: rgba(0,0,0,0.3);
	.cardboard-modal-content {
		margin: 5% auto;
		width: 70vw;
		.card {
			border: none; }
		@media (max-width: $media-lg) {
			margin: 2.5% auto;
			width: 90vw; } } }

.cardboard-modal-small {
	.cardboard-modal-content {
		margin: 15% auto;
		width: 50vw; } }

.cardboard-modal-dark {
	background-color: rgba(0,0,0,0.7); }

// Rotate classes to create rotating fontawesome icons with css transitions
.rotate-and-toggle {
	-moz-transition: all 0.25s linear;
	-webkit-transition: all 0.25s linear;
	transition: all 0.25s linear; }

.rotate-and-toggle.rotate-tilt {
	-ms-transform: rotate(135deg);
	-moz-transform: rotate(135deg);
	-webkit-transform: rotate(135deg);
	transform: rotate(135deg); }

.fadeOne.ng-hide-add-active, .flashFade.ng-hide-add-active {
	// display: block!important
	-webkit-transition: 0.25s linear all;
	transition: 0.25s linear all; }

.fadeTwo.ng-hide-remove-active {
	// display: block!important
	-webkit-transition: 0.25s linear all;
	transition: 0.25s linear all;
	-webkit-transition-delay: 0.25s;
	transition-delay: 0.25s; }

.fadeOne.ng-hide-remove-active {
	// display: block!important
	-webkit-transition: 0.25s linear all;
	transition: 0.25s linear all;
	-webkit-transition-delay: 0.25s;
	transition-delay: 0.25s; }

.ng-hide {
	opacity: 0; }
